      if (this.slide[slider] < max) {
        this.slide[slider]++;
      } else {
        this.slide[slider] = 1;
      }
    },
    retroSlide: function retroSlide(slider, max) {
      this.slide_left = true;

      if (this.slide[slider] > 1) {
        this.slide[slider]--;
      } else {
        this.slide[slider] = max;
      }
    },
    playVideo: function playVideo(contenedor, vid, top) {
      var that = this;
      var bound = document.querySelector(contenedor);
      var video = document.querySelector(vid);

      var scrollVideo = function scrollVideo() {
        if (video.duration) {
          var distanceFromTop = video.offsetTop + bound.offsetParent.offsetTop + (top ? that.wheight / top : 0);
          var rawPercentScrolled = (window.scrollY - distanceFromTop) / (distanceFromTop + bound.scrollHeight);
          var percentScrolled = Math.min(Math.max(rawPercentScrolled, 0), 1);
          video.currentTime = video.duration * percentScrolled;
        }

        requestAnimationFrame(scrollVideo);
      };

      requestAnimationFrame(scrollVideo);
    }
  },
  mounted: function mounted() {
